import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Cell, List, connect, Button, Group, Div } from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Live from '@vkontakte/icons/dist/24/live';
import Icon24LinkCircle from '@vkontakte/icons/dist/24/link_circle';

const osname = platform();

const Profile = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Настройки
		</PanelHeader>
              <List>
                <Cell expandable onClick={() => this.setState({ activePanel: 'nothing' })}>Учетная запись</Cell>
              </List>
			  <Group title="Дополнительно">
              <List>
			  <Div>
              <Button level="secondary"  component="a" href="https://vk.com/ithube" before={<Icon24LinkCircle/>} size="l">Группа ВКонтакте</Button>
             </Div>
			 <Div>
             <Button level="secondary" before={<Icon24Live/>} size="l" onClick={()=>connect.send("VKWebAppAddToFavorites", {})} > Добавить в избранное</Button>
            </Div>
              </List>
            </Group>
	</Panel>
);

Profile.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Profile;